import React, { Component, Fragment } from 'react';
import { Row, Form, Col, Input, DatePicker } from 'antd';
import { Btn, BtnExport, BtnSave } from 'components/common/button';
import { TableForm, InputOnlyNumber } from 'components/common';
import moment from 'moment';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = (updateDiploma, exportPDF) => {
    const columns1 = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'majors',
        key: 'name',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBCTN',
        dataIndex: 'totalGrade',
        key: 'totalGrade',
      },
      {
        title: 'Xếp loại TN',
        dataIndex: 'gradeClassificationLabel',
        key: 'gradeClassificationLabel',
      },
      {
        title: 'Số hiệu bằng',
        dataIndex: 'signNo',
        key: 'signNo',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'approvalNo',
        key: 'approvalNo',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => <span>{record.approvalDate && moment(record.approvalDate).format('DD-MM-YYYY')}</span>,
      },
      {
        title: 'Số vào sổ',
        dataIndex: 'signNoInTrack',
        key: 'signNoInTrack',
        render: (text, record) => {
          const { getFieldDecorator } = this.props.form;
          const { signNoInTrack } = record;
          return signNoInTrack ?
            <span>{record.signNoInTrack}</span>
            :
            <Form.Item >
              {getFieldDecorator(`signNoInTrack-${record.autoKey}`, {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng điền số vào sổ!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
        },
      },
      {
        title: 'Ngày cấp',
        dataIndex: 'returnDate',
        key: 'returnDate',
        render: (text, record) => {
          const { getFieldDecorator } = this.props.form;
          const { returnDate } = record;
          return returnDate ?
            <span>{moment(record.returnDate).format('DD-MM-YYYY')}</span>
            :
            <Form.Item>
              {getFieldDecorator(`returnDate-${record.autoKey}`, {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng điền ngày nhận bằng!',
                  },
                ],
              })(
                <DatePicker
                  format={this.dateFormatList}
                />
              )}
            </Form.Item>
        },
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => {
          return (<Btn
            title="Xem"
            onClick={() => exportPDF(record.studentId)}
          />)
        },
      },
    ];
    return columns1;
  };

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      createMultipleSignNo,
      saveMultipleDiploma,
      exportPDF,
      exportPDFNote,
      disableNoSelectedRowBtn,
      updateDiploma,
    } = this.props;
    const columns = this.createColumns(updateDiploma, exportPDF);
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    return (
      <Fragment>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Col xs={24} sm={12} md={8}>
              <Form.Item label="Số QĐ TN">
                {getFieldDecorator('approvalNo', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8}>
              <Form.Item {...formItemLayout} label="Ngày QĐ TN">
                {getFieldDecorator('approvalDate', {
                  rules: [requiredRule],
                })(
                  <DatePicker
                    defaultValue={moment(new Date(), this.dateFormatList[0])}
                    format={this.dateFormatList}
                    style={{ width: '100%' }}
                  />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8}>
              <Form.Item {...formItemLayout} label="Người ký">
                {getFieldDecorator('signedBy', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item {...formItemLayout} label="Đầu số bằng">
                {getFieldDecorator('signNoHead', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item {...formItemLayout} label="Số bằng từ">
                {getFieldDecorator('signNoFrom', {
                  rules: [requiredRule],
                })(
                  <InputOnlyNumber placeholder="Từ số" />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item {...formItemLayout} label="Số bằng đến">
                {getFieldDecorator('signNoTo', {
                  rules: [requiredRule],
                })(
                  <InputOnlyNumber placeholder="Đến số" />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item {...formItemLayout} label="Đuôi số bằng">
                {getFieldDecorator('signNoTail', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <div className="btn-group" style={{ float: 'right' }}>
              <Btn icon="book" className="btnSave" title="Lập số hiệu bằng" onClick={createMultipleSignNo} />
              <BtnSave title="Lưu thông tin văn bằng" onClick={saveMultipleDiploma} disabled={disableNoSelectedRowBtn} />
              <BtnExport title="Xuất ghi chú văn bằng" onClick={exportPDFNote} />
            </div>
          </Row>
          <TableForm
            reload={reload}
            data={data || []}
            handleSelectedRows={handleSelectedRows}
            rowKey={rowKey}
            columns={columns}
            form={form}
            ref={this.refTable}
          />
        </Form>
      </Fragment>
    );
  }
}

export default TableQualifiedDoThesis;



